@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: white;

}

.nav-bar {
    color: aliceblue;
    background: #0f0c29;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #24243e, #302b63, #0f0c29);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    /* margin: 0 300px 0     0 ;     */
    height: 100%;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.logo {
    display: flex;
    align-items: center;
    gap: .625rem;
    padding-top: 25px;
    margin-bottom: 35px;
}

.logo>img {
    width: 100%;
    max-width: 2.5rem;
}

.logo>p {
    font-family: 'Roboto', sans-serif;
    font-size: 150%;
}

.nav-content {
    margin-left: 10px;
}

ul>li {
    padding-top: .625rem;
    list-style: none;
    margin-bottom: 10px;
    font-size: 105%;
    width: 85%;
    height: 3.125rem;
    /* border: 2px solid red; */


}

i {
    margin-right: 15px;
}

li:hover {
    background: #342e86;
}

.main {
    display: flex;
}


.right {

    width: 100vw;
}

Link {
    text-decoration: none;

}