@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;600&display=swap');


.home-container,
.currency-container,
.news-container {
    background-color: #ebeced;
    margin: 0;
    height: auto;
    padding: 10px 0;
}

.home-container {
    height: auto;
}


.currency-container {
    padding: 10px 0;
}

.currency-container> {
    border: 2px solid red
}

.bars {
    /* border: 2px solid red; */
    position: absolute;
    display: none;
    width: 35px;
}

.cross {
    display: none;
    width: 100%;
    max-width: 15px;
}

.top {
    padding: 2% 0 4% 2%;
    display: flex;
    flex-wrap: wrap;
    gap: 50%;
}

.top>h2 {
    font-size: 5px;
}

.heading {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 1% 1.5% 2.5%;
}

#top10 {
    font-size: 180%;
    font-family: 'Nunito', sans-serif
}

.showmore {
    color: rgb(35, 101, 253);
    font-size: 135%;
    font-weight: 600;
}

#creds {
    text-align: center;

    margin-right: calc(100% - 285px);
}


/* -----------------------GRID------------------------ */

.grid {
    display: grid;
    grid-template-columns: auto auto auto auto;
    column-gap: 20px;
    row-gap: 1.375rem;
    margin-left: 1.875rem;
}

.items {
    width: 280px;
    height: 200px;
    border-radius: .8%;
    /* border: 2px solid red; */
    background: white;
}

hr {
    margin-top: .9375rem;
    color: #ebecede0;
}

.aboveline {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4% 6% 0 4%;
}


.icon {
    width: 100%;
    max-width: 2.5rem;
}


.rank-name {

    margin-left: 15px;
    font-family: 'Comfortaa', cursive;
    font-weight: 600;
}

.data>p {
    margin-left: 30px;
    margin-top: .9375rem;
    font-family: 'Comfortaa', cursive;
}

/* ---
NEWS
------*/

#crypto-news {
    padding: 50px 0 50px 35px;
}

.news-grid {
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 20px;
    row-gap: 1.375rem;
    margin-left: 1.875rem;
}

.news-items {
    width: 380px;
    height: 100%;
    border-radius: .8%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: white;
}

.news-desc>p {
    padding: 15px;
    margin-top: .9375rem;
    font-family: 'Comfortaa', cursive;
}

/* .data {
    height: 100%;
    padding: 15px;
} */

.abovenewsline {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}


.cryptoheadline {

    width: calc(100% - 120px);
    font-weight: bold;
    font-size: 20px;
}

.news-items>a {
    color: black;
}


/* -------
LOADER
-----------*/

.intro {
    width: 100%;
    height: 100vh;

    background: radial-gradient(111% 111% at 74.29% -11%, #A93300 0%, #005570 100%), linear-gradient(127.43deg, #00D5C8 0%, #2200AA 100%);
    background-blend-mode: difference, normal;

    animation-fill-mode: none;
    animation: example 3.5s ease 2s 1 forwards;
    /* animation-duration: 3.5s;
    animation-delay: 2s;
    animation-timing-function: ease;
    animation-fill-mode: forwards; */
}

@keyframes example {
    0% {
        height: 100vh;
    }

    100% {
        height: 0vh;

    }
}


.intro>p {
    position: relative;
    top: 50%;
    right: 1%;
    color: #fff;
    font-size: 150%;
    font-family: 'Sriracha', cursive;
    /* border: 2px solid red;  */
    width: 4000px;
    overflow: hidden;

    border-right: 0.15em solid orange;

    white-space: nowrap;

    margin: 0 auto;

    letter-spacing: 0.15em;

    animation: typing 1.5s steps(50, end),
        blink-caret 0.5s step-end infinite,
        disappear 1.5s ease 2s;
    animation-fill-mode: forwards;


}

@keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 23%;
    }
}


@keyframes blink-caret {

    from,
    to {
        border-color: transparent;
    }

    50% {
        border-color: orange;
    }
}

@keyframes disappear {
    from {
        opacity: 1
    }

    to {
        opacity: 0
    }
}

























/* -------------------
NAVBAR RESPONSIVE
-----------------------*/

@media screen and (min-width:300px) and (max-width:480px) {

    /* LOADER */
    @keyframes typing {
        from {
            width: 0;
        }

        to {
            width: 83%;
        }
    }

    /* LOADER ENDS */
    /* NAVBAR */
    .nav-bar {
        width: 0px;
    }

    .right {
        padding-top: 45px;
        background: #ebeced;
    }

    .bars {
        display: block;
        margin-bottom: 125px;
        margin-left: 5px;
    }

    .cross {
        border-radius: 50%;

        cursor: pointer;
        margin-top: 15px;
        ;
        margin-left: calc(100% - 35px);
        display: block;
        width: 70%;
    }

    /* NVABAR ENDS  */

    .home-container {
        height: 100%;
    }

    .top>p,
    .top>h2,
    .heading>p {
        text-align: center;
    }

    .heading {
        margin: 15px 0;
    }

    .grid,
    .news-grid {
        /* border: 2px solid red; */
        margin-left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .main-grid {

        border: 2px solid red;
        display: block;
        margin: 0 auto;
    }

    .items,
    .news-items {
        align-self: center;
    }

    #creds {
        text-align: center;
        margin-left: 1.125rem;
        width: 205px
    }
}


